      display: none; }
  .mdl-demo .mdl-weather .mdl-weather--decoration .mdl-button--icon .material-icons {
    font-size: 40px;
    transform: translate(-20px, -12px); }

.mdl-demo .mdl-weather .mdl-weather--header {
