  line-height: 24px; }

.mdl-demo .mdl-dropdown {
  display: flex;
  position: relative;
  flex-direction: row;
  align-items: center; }
  .mdl-demo .mdl-dropdown .mdl-button--icon {
    margin-left: 5px;
    margin-top: 2px; }
  .mdl-demo .mdl-dropdown h6 {
    margin: 5px 0 15px; }
    .mdl-demo .mdl-dropdown h6 ~ .mdl-button--icon {
      margin-top: -15px; }

.mdl-demo .mdl-progress {
  width: 100%; }
  .mdl-demo .mdl-progress .bar1, .mdl-demo .mdl-progress .bar3 {
    background-color: #ff4081 !important;
