  display: none; }

.mdl-demo .mdl-layout:not(.is-small-screen) .mdl-layout__tab-bar, .mdl-demo .mdl-layout:not(.is-small-screen) .mdl-layout__tab-bar-container {
  overflow: visible; }

.mdl-demo .mdl-layout .mdl-layout__tab-bar {
  padding: 0;
  padding-left: 16px;
