  height: 100%;
  width: 100%; }
  @media (max-device-width: 767px) {
    .mdl-demo .mdl-layout .mdl-layout__tab-bar {
      display: -ms-flexbox;
      display: flex;
      position: relative;
      -ms-flex-direction: row;
      flex-direction: row;
      -ms-flex-align: center;
      align-items: center; }
      .mdl-demo .mdl-layout .mdl-layout__tab-bar h4 {
        margin-top: 14px; } }
  .mdl-demo .mdl-layout .mdl-layout__tab-bar .mdl-layout__tab {
