    line-height: 64px; }
    .mdl-demo .mdl-layout .mdl-layout__tab-bar .mdl-layout__tab.is-active::after {
      background-color: white;
      height: 4px; }

.mdl-demo .mdl-layout__tab-bar-button {
  display: none; }

.mdl-demo .mdl-layout__tab-bar-container {
