      background-color: transparent; }

.mdl-demo #add {
  position: absolute;
  right: 28px;
  top: -28px;
  z-index: 999; }

.mdl-demo .mdl-layout__content section:not(:last-of-type) {
  position: relative;
  margin-bottom: 48px; }

.mdl-demo .mdl-layout__content section.section--center {
  max-width: 860px; }

.mdl-demo #features section {
  margin-bottom: 72px; }
  .mdl-demo #features section.section--center {
