  align-items: center;
  justify-content: flex-start;
  flex-grow: 0;
  flex-shrink: 1; }

.mdl-demo section .section__text {
  flex-grow: 1;
  flex-shrink: 0;
  padding-top: 14px; }
  @media (max-device-width: 767px) {
    .mdl-demo section .section__text {
      padding-bottom: 20px; } }
  .mdl-demo section .section__text h5 {
