.mdl-demo section .section__circle-container > .section__circle-container__circle {
  width: 64px;
  height: 64px;
  border-radius: 32px;
  margin: 8px 0;
  color: white;
  line-height: 64px;
  text-align: center;
  font-size: 30px; }

.mdl-demo section.section--footer {
  padding: 64px 0;
  margin: 0 -8px -8px -8px; }
  .mdl-demo section.section--footer .section__circle--big {
    width: 100px;
    height: 100px;
    border-radius: 50px;
    margin: 8px 32px; }

.mdl-demo section.section--center .section__text:not(:last-child) {
  border-bottom: 1px solid rgba(0, 0, 0, 0.13); }

.mdl-demo .is-small-screen section.section--footer .section__circle--big {
  width: 50px;
  height: 50px;
  border-radius: 25px;
  margin: 8px 16px; }

.mdl-demo .mdl-card .mdl-card__supporting-text > h3:first-child {
  margin-bottom: 24px; }

.mdl-demo .toc {
  border-left: 4px solid #C1EEF4;
  margin: 24px;
  padding: 0;
  padding-left: 8px;
  display: flex;
  flex-direction: column; }
  .mdl-demo .toc h4 {
    font-size: 0.9rem;
    margin-top: 0; }
  .mdl-demo .toc a {
    color: #4DD0E1;
    text-decoration: none;
    font-size: 16px;
    line-height: 28px;
    display: block; }

.mdl-demo .mdl-menu__container {
  z-index: 99; }

.mdl-demo #module_view > section > .mdl-card {
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  background: transparent; }
  .mdl-demo #module_view > section > .mdl-card > .mdl-card--square {
    margin: 0 10px 10px 0;
    width: 330px;
    height: 300px; }
    .mdl-demo #module_view > section > .mdl-card > .mdl-card--square[data-forecast='sunny'] .mdl-card__supporting-text {
      background-image: url("/images/forecast/sunny.png"); }
    .mdl-demo #module_view > section > .mdl-card > .mdl-card--square[data-forecast='cloudy'] .mdl-card__supporting-text {
      background-image: url("/images/forecast/cloudy.png"); }
    .mdl-demo #module_view > section > .mdl-card > .mdl-card--square[data-forecast='rain'] .mdl-card__supporting-text {
      background-image: url("/images/forecast/rain.png"); }
    .mdl-demo #module_view > section > .mdl-card > .mdl-card--square[data-forecast='snow'] .mdl-card__supporting-text {
      background-image: url("/images/forecast/snow.png"); }
    .mdl-demo #module_view > section > .mdl-card > .mdl-card--square .mdl-card__supporting-text {
