        right: 0;
        font-size: 20px;
        bottom: 24px;
        text-align: right;
        line-height: 24px; }

.mdl-demo .mdl-badge[data-badge]:after {
  width: 43px;
  border-radius: 15px;
  top: -6px;
  right: -20px; }
  @media (max-device-width: 767px) {
    .mdl-demo .mdl-badge[data-badge]:after {
      right: -5px; } }

.mdl-demo .mdl-card--square .mdl-badge[data-badge]:after {
  top: 0px;
  right: -35px; }

.mdl-demo f-dialog {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 50; }
  .mdl-demo f-dialog dialog {
    border: none;
    box-shadow: 0 9px 46px 8px rgba(0, 0, 0, 0.14), 0 11px 15px -7px rgba(0, 0, 0, 0.12), 0 24px 38px 3px rgba(0, 0, 0, 0.2);
    width: 280px;
    z-index: 100;
    top: 30%; }
    .mdl-demo f-dialog dialog .modal-hidden-input {
