      opacity: 0;
      z-index: -1; }
    .mdl-demo f-dialog dialog::backdrop {
      position: fixed;
      top: 0px;
      right: 0px;
      bottom: 0px;
      left: 0px;
      background: rgba(0, 0, 0, 0.0980392); }
